import { DnaPaymentsErrorType } from "./DnaPaymentsErrorType";

export class DnaPaymentsError extends Error {

    public constructor(options: Options) {

        super(options.message);

        Object.setPrototypeOf(this, DnaPaymentsError.prototype);

        this.name = 'DnaPaymentsError';
        this.type = options.type;
        this.code = options.code;
        this.stack = Error().stack;
    }

    public code: string
    public type: DnaPaymentsErrorType
    public toJSON() {
        return {
            ...this,
            message: this.message
        }
    }
}

interface Options {
    code: string
    message: string
    type: DnaPaymentsErrorType
}