import { v4 as uuidv4 } from 'uuid';
import Bus from 'framebus'
import EventEmitter from '@braintree/event-emitter'
import { Client } from 'payment-core'
import { HostedFieldsOptionModel } from "@/models/HostedFieldsOptionModel";
import { injectFrame } from "@/services/injectFrame";
import { PREFIX_FIELD_NAME, URLS, VERIFIED_DOMAINS, events, INTEGRATION_TIMEOUT_MS, externalEvents, externalClasses, threeDSecureEvents } from "@/constants";
import { FieldType } from '../models/FieldType';
import { DnaPaymentsError } from '../models/DnaPaymentsError';
import errors from '@/errors';
import { InputEventDataType, InputEventNameType, InputEventPayloadType } from '@/types'
import { isAllowedField } from '../services';

type FrameReadyEventReplyType = (merchantOptions: HostedFieldsOptionModel) => void
type FrameReadyEventDataType = { field: FieldType }

export class HostedFields extends EventEmitter {

    // private _client: Client = null
    private _threeDSecureFrame: HTMLIFrameElement
    private _isInitialized = false
    private _instanceId = uuidv4()
    private _bus: Bus

    constructor (private options: HostedFieldsOptionModel) {
        super()

        this._bus = new Bus({
            channel: this._instanceId,
            verifyDomain: (domain) => {
                console.log('domain', domain)
                return VERIFIED_DOMAINS.indexOf(domain) >= 0
            }
        })
    }

    init(): Promise<HostedFields> {
        const self = this

        return new Promise((resolve, reject) => {
            if (this._isInitialized) {
                resolve(self)
                return
            }

            const frameReadyPromises: Promise<FrameReadyEventReplyType>[] = []
            const frameReadyResolveFunctions: Partial<Record<FieldType, (value: FrameReadyEventReplyType) => void>> = {}
    
            Object.entries(self.options.fields).forEach(([fieldType, field]) => {
                const { container } = field
                const iframe = injectFrame(`${PREFIX_FIELD_NAME}${fieldType}`, container)
                iframe.src = `${URLS.frameUrl}#${self._instanceId}`
    
                frameReadyPromises.push(new Promise((resolve) => {
                    frameReadyResolveFunctions[fieldType] = resolve
                }))
            })
    
            self._bus.on(events.FRAME_READY, (data: FrameReadyEventDataType, reply: FrameReadyEventReplyType) => {
                if (data && data.field && isAllowedField(data.field)) {
                    frameReadyResolveFunctions[data.field](reply)
                }
            })

            this._threeDSecureFrame = injectFrame(`${PREFIX_FIELD_NAME}three-d-secure`, self.options.threeDSecure.container, { minHeight: '400px' })
            this._threeDSecureFrame.src = `${URLS.threeDSecureUrl}#${self._instanceId}`
            self._bus.on(threeDSecureEvents.FRAME_READY, (data, reply) => {
                reply(self.options)
            })

            self.attachBusEvents.bind(self)()

            const failureTimeout = setTimeout(() => {
                reject(new DnaPaymentsError(errors.HOSTED_FIELDS_TIMEOUT))
            }, INTEGRATION_TIMEOUT_MS)

            Promise.all(frameReadyPromises).then((replies) => {
                replies.forEach((reply) => reply(self.options))
                clearTimeout(failureTimeout)
                self._isInitialized = true
                resolve(self)
            })
        })
    }

    submit(data: any) {
        return new Promise((resolve, reject) => {
            this._bus.emit(events.SUBMIT, data, (payload: any) => {
                console.log('payload', payload)
                const [err, result] = payload
                if (err) {
                    reject(err)
                } else {
                    console.log('result.secure3D', Boolean(result.secure3D))
                    if (result.secure3D) {
                        this._bus.emit(threeDSecureEvents.SET_DATA, result, ([error, m]) => {
                            if (error) {
                                reject(error)
                            } else {
                                resolve(result)
                            }
                        })
                    } else {
                        resolve(result)
                    }
                }
            })
        })
    }

    clear() {
        this._bus.emit(events.CLEAR)
    }

    attachBusEvents() {
        this._bus.on(events.INPUT_EVENT, (eventData: InputEventDataType) => {
            const { type, payload } = eventData
            const { fieldKey, fieldsState } = payload
            const { classContainer, container } = this.options.fields[fieldKey];

            const elem = classContainer || container
            const state = fieldsState[fieldKey]

            elem.classList.toggle(externalClasses.FOCUSED, state.isFocused);
            elem.classList.toggle(externalClasses.VALID, state.isTouched && state.isValid);
            elem.classList.toggle(externalClasses.INVALID, state.isTouched && !state.isValid);

            this._emit(type, payload)
        })

        this._bus.on(threeDSecureEvents.SHOW, (data) => {
            this._emit(threeDSecureEvents.SHOW, data)
        })

        this._bus.on(threeDSecureEvents.HIDE, (data) => {
            this._emit(threeDSecureEvents.HIDE, data)
        })
    }

    on(event: InputEventNameType, callback: (data: InputEventPayloadType) => void) {
        super.on(event, callback)
    }

    destroy = () => {
        this._bus.teardown()

    }
}
