import { createFrame } from "@/lib/createFrame";

export function injectFrame(name: string, container: HTMLElement, style = {}) {
    const iframe = createFrame({
        name,
        style: Object.assign({}, iFrameStyle, style)
    })
    const fragment = document.createDocumentFragment()
    const clearboth = document.createElement('div')
    clearboth.style.clear = 'both'
    fragment.appendChild(iframe)
    fragment.appendChild(clearboth)
    container.innerHTML = ''
    container.appendChild(fragment)

    return iframe
}

const iFrameStyle = {
    border: 'none',
    width: '100%',
    height: '100%',
    'float': 'left'
}
