import { HostedFieldsOptionModel } from "@/models/HostedFieldsOptionModel";
import { HostedFields } from "./classes/HostedFields";

export async function create(options: HostedFieldsOptionModel) {
    const instance = new HostedFields(options)
    await instance.init()
    return instance
}

if (!window.dnaPayments) {
    window.dnaPayments = {}
}

window.dnaPayments.hostedFields = {
    create
}
